@import url('https://fonts.googleapis.com/css2?family=Space+Grotesk:wght@400;500;600;700&family=Fira+Code:wght@400;500;600&display=swap');

@tailwind base;
@tailwind components;
@tailwind utilities;

/* Design System - Hacker/Cyberpunk Theme */
@layer base {
    :root {
        /* Core Colors - Dark Mode Default (Hacker Theme) */
        --background: 220 15% 6%;
        --foreground: 160 100% 80%;
        
        /* Card and surfaces */
        --card: 220 18% 8%;
        --card-foreground: 160 100% 85%;
        
        /* Popover */
        --popover: 220 18% 10%;
        --popover-foreground: 160 100% 85%;
        
        /* Primary - Matrix Green */
        --primary: 160 100% 50%;
        --primary-foreground: 220 15% 6%;
        
        /* Secondary */
        --secondary: 220 20% 15%;
        --secondary-foreground: 160 90% 75%;
        
        /* Muted */
        --muted: 220 15% 18%;
        --muted-foreground: 160 30% 55%;
        
        /* Accent - Cyan glow */
        --accent: 180 100% 50%;
        --accent-foreground: 220 15% 6%;
        
        /* Destructive */
        --destructive: 0 70% 50%;
        --destructive-foreground: 0 0% 100%;
        
        /* Border and input */
        --border: 160 50% 20%;
        --input: 220 18% 12%;
        --ring: 160 100% 50%;
        
        /* Charts */
        --chart-1: 160 100% 50%;
        --chart-2: 180 100% 50%;
        --chart-3: 140 80% 45%;
        --chart-4: 200 90% 55%;
        --chart-5: 120 70% 40%;
        
        /* Radius */
        --radius: 0.5rem;
        
        /* Custom Hacker Theme Tokens */
        --glow-primary: 0 0 40px hsl(160 100% 50% / 0.4);
        --glow-accent: 0 0 30px hsl(180 100% 50% / 0.3);
        --glow-text: 0 0 10px hsl(160 100% 50% / 0.6);
        --gradient-hacker: linear-gradient(135deg, hsl(220 15% 8%) 0%, hsl(220 18% 12%) 50%, hsl(220 15% 6%) 100%);
        --gradient-matrix: linear-gradient(180deg, hsl(160 100% 50% / 0.1) 0%, transparent 100%);
        --gradient-glow: linear-gradient(135deg, hsl(160 100% 50% / 0.15) 0%, hsl(180 100% 50% / 0.1) 100%);
        --border-glow: 1px solid hsl(160 100% 50% / 0.3);
        --transition-smooth: 0.4s cubic-bezier(0.4, 0, 0.2, 1);
        --transition-bounce: 0.5s cubic-bezier(0.34, 1.56, 0.64, 1);
        
        /* Terminal Colors */
        --terminal-green: 160 100% 50%;
        --terminal-cyan: 180 100% 60%;
        --terminal-yellow: 50 100% 60%;
        --terminal-red: 0 80% 60%;
        --terminal-purple: 280 70% 60%;
    }
    
    /* Light Mode (Scroll-triggered section) */
    .light {
        --background: 60 10% 96%;
        --foreground: 220 20% 15%;
        
        --card: 0 0% 100%;
        --card-foreground: 220 20% 15%;
        
        --popover: 0 0% 100%;
        --popover-foreground: 220 20% 15%;
        
        --primary: 160 80% 35%;
        --primary-foreground: 0 0% 100%;
        
        --secondary: 60 5% 92%;
        --secondary-foreground: 220 20% 20%;
        
        --muted: 60 5% 90%;
        --muted-foreground: 220 15% 40%;
        
        --accent: 180 70% 40%;
        --accent-foreground: 0 0% 100%;
        
        --destructive: 0 65% 50%;
        --destructive-foreground: 0 0% 100%;
        
        --border: 220 15% 85%;
        --input: 220 10% 92%;
        --ring: 160 80% 35%;
        
        --glow-primary: 0 4px 20px hsl(160 80% 35% / 0.15);
        --glow-accent: 0 4px 15px hsl(180 70% 40% / 0.1);
        --gradient-hacker: linear-gradient(135deg, hsl(60 10% 98%) 0%, hsl(60 5% 96%) 100%);
        --gradient-matrix: linear-gradient(180deg, hsl(160 80% 35% / 0.05) 0%, transparent 100%);
    }
}

@layer base {
    * {
        @apply border-border;
    }
    
    html {
        scroll-behavior: smooth;
    }
    
    body {
        @apply bg-background text-foreground antialiased;
        font-family: 'Space Grotesk', -apple-system, sans-serif;
    }
    
    code, .font-mono {
        font-family: 'Fira Code', 'Menlo', monospace;
    }

    [data-debug-wrapper="true"] {
        display: contents !important;
    }
}

@layer components {
    /* Glowing text effect */
    .text-glow {
        text-shadow: var(--glow-text);
    }
    
    /* Terminal cursor blink */
    .cursor-blink::after {
        content: 'â–ˆ';
        animation: blink 1s step-end infinite;
    }
    
    @keyframes blink {
        50% { opacity: 0; }
    }
    
    /* Matrix rain effect container */
    .matrix-bg {
        background: var(--gradient-hacker);
        position: relative;
        overflow: hidden;
    }
    
    .matrix-bg::before {
        content: '';
        position: absolute;
        inset: 0;
        background: var(--gradient-matrix);
        pointer-events: none;
    }
    
    /* Scanline effect */
    .scanlines::after {
        content: '';
        position: absolute;
        inset: 0;
        background: repeating-linear-gradient(
            0deg,
            transparent,
            transparent 2px,
            hsl(160 100% 50% / 0.03) 2px,
            hsl(160 100% 50% / 0.03) 4px
        );
        pointer-events: none;
    }
    
    /* Glowing border effect */
    .border-glow {
        border: var(--border-glow);
        box-shadow: var(--glow-primary);
    }
    
    /* Glass effect for cards */
    .glass {
        background: hsl(var(--card) / 0.7);
        backdrop-filter: blur(20px);
        -webkit-backdrop-filter: blur(20px);
    }
    
    /* Hover glow effect */
    .hover-glow {
        transition: box-shadow var(--transition-smooth), transform var(--transition-smooth);
    }
    
    .hover-glow:hover {
        box-shadow: var(--glow-primary);
        transform: translateY(-2px);
    }
    
    /* Animated underline */
    .animated-underline {
        position: relative;
    }
    
    .animated-underline::after {
        content: '';
        position: absolute;
        bottom: -2px;
        left: 0;
        width: 0;
        height: 2px;
        background: hsl(var(--primary));
        transition: width var(--transition-smooth);
    }
    
    .animated-underline:hover::after {
        width: 100%;
    }
    
    /* Typing animation */
    .typing-animation {
        overflow: hidden;
        white-space: nowrap;
        animation: typing 3s steps(40, end);
    }
    
    @keyframes typing {
        from { width: 0; }
        to { width: 100%; }
    }
    
    /* Pulse glow animation */
    .pulse-glow {
        animation: pulse-glow 2s ease-in-out infinite;
    }
    
    @keyframes pulse-glow {
        0%, 100% { box-shadow: 0 0 20px hsl(160 100% 50% / 0.3); }
        50% { box-shadow: 0 0 40px hsl(160 100% 50% / 0.6); }
    }
    
    /* Scroll indicator */
    .scroll-indicator {
        animation: scroll-bounce 2s ease-in-out infinite;
    }
    
    @keyframes scroll-bounce {
        0%, 100% { transform: translateY(0); }
        50% { transform: translateY(10px); }
    }
    
    /* Gradient text */
    .gradient-text {
        background: linear-gradient(135deg, hsl(160 100% 50%), hsl(180 100% 60%));
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;
        background-clip: text;
    }
    
    /* Section transition wrapper */
    .section-transition {
        transition: background-color 0.8s ease-in-out, color 0.8s ease-in-out;
    }
}

@layer utilities {
    .animation-delay-100 { animation-delay: 100ms; }
    .animation-delay-200 { animation-delay: 200ms; }
    .animation-delay-300 { animation-delay: 300ms; }
    .animation-delay-400 { animation-delay: 400ms; }
    .animation-delay-500 { animation-delay: 500ms; }
}
